@import '../../assets/styles/setup/variables';
.app-header {
    padding:$main-padding;
    height: 80px;
    display: flex;
    color: $main-clr;
    align-items: center;
    justify-content: space-between;
    img{
        width: 100px;
    }
    ul{
        list-style: none;
        padding:0;
        margin:0;
        display: flex;
        align-items: center;
        a{
            text-decoration: none;
            color: $main-clr;
            padding:10px;
        }
    }
    .active{
        color:$main-clr-dark;
    }
}