@import '../../assets/styles/setup/variables';
.contact-preview {
    padding:30px;
    box-shadow: 0 0 0 0.1px black;
    border-radius: 2rem;
    display: flex;
    justify-content: center;
    cursor: pointer;
    img{
        width: 50px;
        height: 50px;
    }
    .preview-details{
        display: flex;
        flex-direction: column;
        p{
            color:$main-clr;
        }
        p:not(:first-of-type){
            color:rgb(146, 145, 145);
        }
    }

}